<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5

    .titulo-principal
      .titulo-principal__numero
        .h3 1
      .h3 Elicitacin de requisitos
    p.mb-4 El propsito de la elicitacin de requerimientos es ganar conocimientos relevantes del problema, que se utilizarn para producir una especificacin formal del software necesario para resolverlo.
    .row.mb-5.align-items-center
      .col-lg-7
        .bloque-texto-b.p-4
          .bloque-texto-b__texto.h2.mb-0 Un problema puede ser definido como la diferencia entre las cosas como se perciben y las cosas como se desean
          br
          p(style="position: relative;") -(Gause y Weinberg 1989)
      .col-lg-5.mt-4.mt-lg-0
        p Aqu se ve la importancia que tiene una buena comunicacin entre desarrolladores y clientes; de esta comunicacin con el cliente depende que sus necesidades queden claras. Adems, al final de la fase de anlisis de requerimientos, el analista podra llegar a tener un conocimiento extenso en el dominio del problema.
    p.mb-4 La elicitacin de requisitos es la actividad que se considera como el primer paso en un proceso de ingeniera de requisitos; su significado hace referencia a la puesta en marcha de tcnicas que sirven para recopilar conocimiento o informacin y los objetivos de esta fase de elicitacin, son:
    .list.align-items-star
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Conocer el dominio del problema para poder comunicarse con clientes y usuarios y entender sus necesidades.
    .list.align-items-star.mt-2
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Conocer el sistema actual (manual o informatizado) y sus aspectos positivos y negativos.
    .list.align-items-star.mt-2.mb-5
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Identificar las necesidades, tanto explcitas como implcitas, de clientes y usuarios y sus expectativas sobre el sistema a desarrollar.
    #t_1_1
    .titulo-segundo.pt-5
      .h4 1.1. Planeacin
    p.mb-5 La planeacin busca definir las tareas a realizar para elegir y planificar las tcnicas a emplear durante la actividad de elicitacin de la fase de ingeniera de requisitos del desarrollo de software. En la siguiente tabla se presenta una relacin de estas tareas y sus correspondientes procesos.
    .tabla-a
      table
        caption Tabla 1/ Tareas para elicitacin de requisitos.
        thead
          tr
            th.table1 Tareas
            th.table1 Proceso
        tbody
          tr
            td #[b A.] Identificar las fuentes.
            td Lista de fuentes de requerimientos.
          tr
            td #[b B.] Identificar interesados del producto.
            td Categoras de los interesados (stakeholder).
          tr
            td #[b C.] Matriz stakeholders (Describir necesidades y criterios de xito).
            td Perfil de stakeholder.
          tr
            td #[b D.] Revisar tcnicas.
            td Identificar combinaciones de tcnicas entrevistas, grupos focales, encuestas, prototipos.
          tr
            td #[b E.] Captura de interesados.
            td Plan de captura de interesados.
    p.mb-5.mt-3  #[b Nota:] tomado de Durn y Bernrdez (2001)
    p.mb-5 A continuacin, se describen los procesos relacionados con las tareas para elicitacin de requisitos:
    .row.align-items-start.mb-4
      .col-lg-6
        h5 #[span.txt-color A.] Identificar las fuentes de requerimientos.
        p Existe un conjunto de fuentes de requisitos en cada proyecto de desarrollo de software, as, usuarios y expertos abastecen de informacin detallada acerca del problema y necesidades del usuario. Los procesos y sistemas existentes representan, tambin, fuentes de requisitos; adems, la documentacin existente como manuales, formularios y reportes, incluso especificaciones de requisitos anteriores, puede proveer informacin til acerca de la organizacin y su entorno.
      .col-lg-6.mt-4.mt-lg-0
        figure
          img(src='@/assets/curso/img2.png', style="max-width: 80%; margin: auto;")
    p.mb-4 En el proceso de esta actividad se identifican:
    .list.align-items-star
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Interesados relevantes.
    .list.align-items-star.mt-2
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Documentacin que se puede usar como fuente de informacin de los requerimientos.
    .list.align-items-star.mt-2.mb-4
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Fuentes de informacin externas.
    p.mb-4 Las fuentes de requerimientos incluyen los propietarios del problema, los stakeholders, documentos y otros sistemas (Pearson, 2002). En ese sentido, los requerimientos pueden obtenerse en diversas fuentes que pueden clasificarse en gente (people), productos o documentos, pero cualquiera sea la fuente de esos requerimientos deben ser chequeados con los stakeholders.
    p.mb-4 Estas fuentes de requerimientos, se pueden clasificar en:
    .hr1
    .row.align-items-center
      .col-2
        figure
          img(src='@/assets/curso/img1.svg', style="max-width: 50%; margin: auto;")
      .col-10
        h4 Fuentes primarias
        p Aportan material de primera mano (es protagonista o testigo de los hechos), estas fuentes contienen informacin original, que ha sido publicada por primera vez y que no ha sido filtrada, interpretada o evaluada por nadie ms.
    .hr2
    .row.align-items-center
      .col-2
        figure
          img(src='@/assets/curso/img2.svg', style="max-width: 50%; margin: auto;")
      .col-10
        h4 Fuentes secundarias
        p Toman y reproducen la informacin que le aport una fuente primaria. Son las que contienen informacin primaria, sintetizada y reorganizada y estn especialmente diseadas para facilitar y maximizar el acceso a las fuentes primarias o a sus contenidos. Parten de datos preelaborados, como pueden ser datos obtenidos de anuarios estadsticos, internet, medios de comunicacin, bases de datos procesadas con otros fines, artculos y documentos relacionados con un tema, libros, tesis, informes oficiales, etc.
    .hr2
    .row.align-items-center
      .col-2
        figure
          img(src='@/assets/curso/img3.svg', style="max-width: 50%; margin: auto;")
      .col-10
        h4 Fuentes terciarias
        p Son guas fsicas o virtuales que contienen informacin sobre las fuentes secundarias. Forman parte de la coleccin de referencia de una biblioteca; facilitan el control y acceso a toda la gama de repertorios de referencia, como las guas de obras de referencia, o a un solo tipo, como las bibliografas.
    .hr3.mb-4
    p.mb-4 Por otra parte, las fuentes de informacin, pueden ser orales, escritas o de otro tipo, dependiendo de cmo se transmitan los datos. A continuacin, se pueden revisar algunos ejemplos de fuentes de informacin.
    figure.mb-5
      img(src='@/assets/curso/img3.png', style="max-width: 100%; margin: auto;")
    .row.align-items-start.mb-4.pt-5
      .col-lg-6
        h5 #[span.txt-color B.] Identificar interesados del producto.
        p.mb-4 Uno de los primeros pasos en el proceso es el anlisis e identificacin de todas las personas relevantes que tienen un grado de inters en el proyecto. #[b Los interesados (stakeholders), son los individuos y organizaciones que estn relacionados activamente en un proyecto de software]; tienen influencia directa o indirecta sobre los requisitos, o sus intereses se ven afectados por el proyecto (Baar, 2006, Ventura, 2002).
        p En resumen, son grupos o individuos que estn interesados en el producto de software que se est desarrollando y necesitarn estar informados acerca del progreso, conflictos, cambios y prioridades del proceso de desarrollo del producto.
      .col-lg-6.mt-4.mt-lg-0
        figure
          img(src='@/assets/curso/img4.png', style="max-width: 80%; margin: auto;")
    p.mb-4 Los stakeholders se dividen en dos grupos:
    .hr1
    .row.align-items-center
      .col-2
        figure
          img(src='@/assets/curso/img4.svg', style="max-width: 50%; margin: auto;")
      .col-10
        h4 Primarios
        p Son aquellas personas indispensables para el correcto funcionamiento de la organizacin, y tienen una relacin econmica directa con la empresa. Estos pueden ser sus socios, clientes y accionistas
    .hr2
    .row.align-items-center
      .col-2
        figure
          img(src='@/assets/curso/img5.svg', style="max-width: 50%; margin: auto;")
      .col-10
        h4 Secundarios
        p Son los entes que no participan directamente de la compaa, pero tambin son afectados por sus resultados. En este crculo se encuentran los competidores, el mercado y las personas en general.
    .hr2.mb-5
    p.mb-4 A continuacin, se listan roles ms generales de las personas involucradas con sus trminos similares, aunque cabe resaltar que existen leves diferencias entre ellos (Sommerville y Sawyer, 2005)
    .list.align-items-star
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Lder de proyecto / Administrador de proyecto / Gerente de proyecto.
    .list.align-items-star.mt-2
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Analista / Ingeniero de requisitos.
    .list.align-items-star.mt-2
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Ingeniero de sistemas / Arquitecto.
    .list.align-items-star.mt-2
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Programador / Desarrollador / Ingeniero de software.
    .list.align-items-star.mt-2
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Probador / Asegurador de la calidad.
    .list.align-items-star.mt-2.mb-4
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Administrador de bases de datos.
    p.mb-5 En la siguiente tabla se presentan los principales roles involucrados en el proceso de ingeniera de requisitos, as como las actividades en las que tienen mayor participacin.
    .tabla-a
      table
        caption Tabla 2/ Roles involucrados en la ingeniera de requisitos.
        thead
          tr
            th.table1(style="width: 260px") ROL
            th.table1 Descripcin
        tbody
          tr
            td #[b Cliente]
            td Representa a la persona u organizacin que solicita la creacin de un sistema a un rea de desarrollo y quien lo paga. Es con quien se negocia el tiempo, costo y alcance del proyecto. Pueden o no ser usuarios del sistema.
          tr
            td #[b Usuario]
            td Son las personas que interactuarn con el sistema. Proporcionan informacin fundamental para el xito del proyecto, ya que conocen y conviven con los procesos diarios.
          tr
            td #[b Lder de proyecto]
            td Por parte del equipo de desarrollo, es el representante ante el cliente. Es la persona responsable de completar el proyecto exitosamente con los recursos dados.
          tr
            td #[b Analista]
            td Su labor se enfoca en la ingeniera de requisitos, los identifica, analiza, modela y documenta. Adems, establece contacto directo con los usuarios y utiliza diversas tcnicas de comunicacin y de recopilacin de informacin para lograr su objetivo.
          tr
            td #[b Programador]
            td Con base en los requisitos recibidos de parte de los ingenieros de requisitos, el programador realiza la codificacin para producir el sistema deseado.
          tr
            td #[b Asegurador de la calidad]
            td Garantiza el cumplimiento del proceso y de los estndares del producto. Enfocado en los requisitos, los verifica y valida para imprimir la calidad desde las primeras etapas del desarrollo. Paralelamente, prepara planes de prueba para esos requisitos del sistema.
          tr
            td #[b Arquitecto]
            td Es el responsable del diseo de alto nivel y es clave a la hora de precisar los atributos de calidad del producto.
    p.mb-5.mt-3  #[b Nota:] tomado de Ventura (2002)
    p.mb-4 Algunas de las tcnicas que se pueden emplear para llevar a cabo la labor de anlisis de los stakeholders incluyen entrevistas con los expertos, lluvia de ideas en grupo y lista de chequeo. Se espera que este grupo de personas identifiquen y caractericen a los stakeholders objetivamente, por tal motivo es recomendable involucrar a personas de diferentes contextos (Karisen, 2002 citado en Wessinger, 2012).
    .row.align-items-start.mb-5.pt-5
      .col-lg-6
        h5 #[span.txt-color C.] Matriz de stakeholders.
        p.mb-4 La utilizacin de esta #[b herramienta de anlisis permite clasificar a los involucrados en el proyecto segn sus niveles de inters y poder sobre l], lo que facilita la priorizacin de los stakeholders ms importantes para desarrollar las estrategias de gestin correspondientes.
      .col-lg-6.mt-4.mt-lg-0
        figure
          img(src='@/assets/curso/img5.png', style="max-width: 80%; margin: auto;")
    h5 Importancia de la matriz de stakeholders en los proyectos de desarrollo.
    p.mb-5 En los proyectos de desarrollo, la gestin de los stakeholders es de suma importancia para alcanzar el xito de los proyectos, ya que el proceso de identificacin de los involucrados y la definicin de sus niveles de inters e influencia en el proyecto, marcarn el punto de partida para desarrollar estrategias que posibilitan obtener el apoyo requerido para alcanzar los objetivos por los que el proyecto es emprendido. Es por ello, que la matriz de stakeholders es una herramienta indispensable desde el comienzo del proyecto mismo, ya que proveer de la informacin necesaria para gestionar, adecuadamente, las expectativas de los involucrados a lo largo del proyecto, maximizando las influencias positivas y mitigando los impactos negativos potenciales derivados de estos. Adems, dado el carcter social de los proyectos de desarrollo, involucrar a la sociedad civil no debe ser solo un ejercicio de comunicacin unidireccional sino una oportunidad para lograr su apoyo al proyecto.
    h5 Proceso de armado de la matriz de stakeholders.
    p.mb-5 Para desarrollar la matriz de stakeholders es necesario identificar las entradas necesarias que proveern la informacin con la que el lder y el equipo de proyecto trabajarn para desarrollar la matriz misma. Tales entradas pueden ser el acta de constitucin de proyecto, documentos de adquisicin, activos de los procesos y factores ambientales de la organizacin.
    figure.mb-5
      img(src='@/assets/curso/img6.png', style="max-width: 100%; margin: auto;")
      figcaption Figura 2 / Entradas, herramientas y tcnicas, salidas.
    .row.mb-4
      .col-lg-6
        a.anexo.mb-4(href="https://todopmp.com/cards/" target="_blank")
          .anexo__icono
            img(src="@/assets/template/icono-link.svg")
          .anexo__texto
            p #[b Anexo.] Para profundizar en detalle, se invita a consultar la Gua PMBOK 6 - 49 procesos, entradas, herramientas y salidas, como material complementario.
    h5 Descripcin de los componentes de la matriz de stakeholders.
    p.mb-4 A continuacin, se presenta el concepto de cada uno de los componentes que estructuran la matriz de stakeholders.
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
      .row(titulo="Stakeholder")
        .col-12
          p Es el nombre con el que se identifica al stakeholder.
      div(titulo="Tipo")
        .row
          .col-12
            p Identifica si el stakeholder desempea un rol interno o externo al proyecto mismo. Los stakeholders pueden ser internos, como el personal de las unidades ejecutoras, el personal administrativo o ejecutivo de la organizacin, el personal de las entidades financiadoras con alto nivel de poder e influencia en el proyecto y sus recursos; o externos como los beneficiarios del proyecto, las instituciones del sector o las organizaciones de la sociedad civil, quienes sern de un modo u otro impactados por los resultados del proyecto.
      div(titulo="Objetivo o resultados")
        .row
          .col-12
            p En este campo se enlistan los objetivos o resultados en los que el stakeholder muestra inters o en aquellos en los que puede influir positiva o negativamente con sus acciones. Esta informacin puede ser suministrada por el acta de constitucin de proyectos, la estructura de la organizacin, la estructura de desglose de trabajo, los diferentes planes que conforman el proyecto, entrevistas a los mismos interesados, etc.
      div(titulo="Acciones posibles con impacto positivo / negativo")
        .row
          .col-12
            p Son las acciones que puede emprender el stakeholder y que pueden influir, negativa o positivamente, en los objetivos del proyecto en los que muestra su inters o en aquellos en los que puede influir debido a su jerarqua, estatus, recursos de los que dispone, entre otros.
      div(titulo="Estrategias")
        .row
          .col-12
            p Es un listado de acciones que se pueden emprender para obtener el apoyo necesario o evitar obstculos por parte de los stakeholders durante la ejecucin y conclusin del proyecto. Las estrategias se desarrollan considerando el tipo de stakeholder, los objetivos en los que est interesado, el nivel de inters y poder que puede ejercer en el proyecto (figura 1) y las acciones posibles que podra emprender para afectar tanto positiva como negativamente al proyecto.
      div(titulo="Conclusiones")
        .row
          .col-12
            p Es la sntesis sobre puntos clave a considerar para gestionar de manera efectiva las expectativas de los stakeholders. Las conclusiones se obtienen de relacionar, analizar y sintetizar toda la informacin vertida en la matriz de stakeholders.
    h5 Categorizacin de stakeholders y estrategias de gestin de las expectativas.
    p.mb-4 Como ya se haba mencionado anteriormente, la matriz de stakholders es una herramienta muy til que permite clasificar a los involucrados en el proyecto segn sus niveles de inters e influencia, priorizando a los ms importantes y desarrollando as las estrategias correspondientes para gestionar sus expectativas. De la misma manera, su clasificacin puede cambiar durante la vida del proyecto. As, aquellos que fueron inicialmente identificados con un alto nivel de influencia en el proyecto, pueden ser reclasificados a un nivel ms bajo durante otras etapas de la vida del proyecto.
    p.mb-4 La categorizacin de los stakeholders se lleva a cabo una vez que la informacin sobre stos est completa. Para ello se puede utilizar una matriz de 2x2 en la que se pueda graficar el grado de poder e inters que tiene el involucrado en el proyecto, coadyuvando as a clasificar a cada stakeholder dentro del grupo para el cual se definen diferentes estrategias (figura 2).
    figure
      img(src='@/assets/curso/img7.png', style="max-width: 100%; margin: auto;")
      figcaption Figura 2 / Ejemplo de matriz inters vs. influencia.
    p.mt-3.mb-5 #[b Nota: ]tomado de Gardnet et al. (1986)
    #t_1_2
    .titulo-segundo.pt-5
      .h4 1.2   Tcnicas e instrumentos para elicitar requisitos
    p.mb-4 Hay una variedad de tcnicas propuestas para ingeniera de requerimientos (Herrera, 2003. p. 12), por lo que es primordial resaltar que estas tcnicas pueden ser aplicables a las distintas fases del proceso de la ingeniera de requerimientos (IR), teniendo en cuenta las caractersticas propias del proyecto en particular que se est desarrollndose para aprovechar al mximo su utilidad.
    p.mb-5 A continuacin, se presentar una serie de tcnicas destinadas a facilitar la elicitacin correcta y efectiva de los requisitos dentro de un proceso de desarrollo.
    h5 1.2.1. Entrevista.
    .row.mb-5
      .col-lg-8
        .bloque-texto-b.p-4
          .bloque-texto-b__texto.h3.mb-0 La entrevista es una forma de recoger informacin de otra persona a travs de una comunicacin interpersonal que se lleva a cabo por medio de una conversacin estructurada.
          br
          p(style="position: relative;") - (Braude, 2003)
    p.mb-5 En las entrevistas se pueden identificar tres fases: preparacin, realizacin y anlisis (Piattini et al. 1996), como se puede observar en el siguiente grfico.
    TabsB
      .py-4.py-md-5(titulo="Preparacin" :icono="require('@/assets/curso/img6.svg')")
        .row.align-items-center
          .col-md-6.mb-4.mb-md-0
            .h4 1. Preparacin
            p.mb-4 El entrevistador debe documentarse e investigar la situacin de la organizacin, analizando los documentos de la empresa disponible.
            .list.align-items-star
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 Se debe intentar minimizar el nmero de entrevistados, considerando las entrevistas de cortesa.
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 Analizar el perfil de los entrevistados.
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 Definir el objetivo y el contenido de la entrevista.
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 Planificar el lugar y la hora en la que se va a desarrollar la entrevista (es conveniente realizarla en un lugar confortable).
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 Algunos proponen enviar previamente el entrevistado un cuestionario y un pequeo documento de introduccin al proyecto de desarrollo.
          .col-md-6
            figure
              img(src='@/assets/curso/img8.png', style="max-width: 80%; margin: auto;")
      .py-4.py-md-5(titulo="Realizacin" :icono="require('@/assets/curso/img7.svg')")
        .row.align-items-center
          .col-md-6.mb-4.mb-md-0
            .h4 2. Realizacin
            p.mb-4 Dentro de la realizacin de las entrevistas se distinguen tres etapas, tal como se expone en Piattini et al. (1996):
            .list.align-items-start.pl-4
              .list__item
                .list__item__bulletNumber
                  span a.
              .list__txt
                p.mb-0.mt-2 #[b Apertura:] presentarse e informar al entrevistado sobre la razn de la entrevista.
            .list.align-items-start.mt-2.pl-4.mb-4
              .list__item
                .list__item__bulletNumber
                  span b.
              .list__txt
                p.mb-0.mt-2 #[b Desarrollo:] cumplir las reglas del protocolo, hay que llegar a un acuerdo sobre cmo se va a registrar la informacin obtenida.
            p Durante esta fase se pueden emplear distintas tcnicas:
          .col-md-6
            figure
              img(src='@/assets/curso/img9.png', style="max-width: 80%; margin: auto;")
        .row.p-4.bg-color1.mt-4
          .col-12
            .list.align-items-star
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 #[b Preguntas abiertas:] tambin denominadas de libre contexto (Gause y Weinberg, 1989), estas preguntas no pueden  responderse con un #[b "s"] o un #[b "no"], permiten una mayor comunicacin y evitan la sensacin de interrogatorio. Por  ejemplo, #[b "Qu se hace para registrar un pedido?"], #[b "Dgame qu se debe hacer cuando un cliente pide una factura"] o #[b Cmo se rellena un recibo?"].
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 #[b Utilizar palabras apropiadas:] se deben evitar tecnicismos que no conozca el entrevistado y palabras o frases que  puedan perturbar emocionalmente la comunicacin (Goleman 1996, Goleman 1999).
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 #[b Mostrar inters en todo momento:] es fundamental cuidar la comunicacin no verbal (Davis, 1985) durante la entrevista:  tono de voz, movimiento, expresin facial.
        .list.align-items-start.pl-4.mt-4
          .list__item
            .list__item__bulletNumber
              span c.
          .list__txt
            p.mb-0.mt-2 #[b Terminacin:] se termina recapitulando la entrevista agradeciendo el esfuerzo y dejando abierta la posibilidad de volver a contactar para aclarar conceptos o bien citndole para otra entrevista.
      .py-4.py-md-5(titulo="Anlisis" :icono="require('@/assets/curso/img8.svg')")
        .row.align-items-center
          .col-md-6.mb-4.mb-md-0
            .h4 3. Anlisis
            p.mb-4 Consiste en leer las notas, pasarlas en limpio, reorganizar la informacin, contrastarlas con otras entrevistas o fuentes de informacin, evaluar cmo ha ido la entrevista.
            p En estas entrevistas, el equipo de la ingeniera de requerimientos hace preguntas sobre el sistema que utilizan y sobre el sistema a desarrollar. Los requerimientos provienen de las respuestas a estas preguntas.
          .col-md-6
            figure
              img(src='@/assets/curso/img10.png', style="max-width: 80%; margin: auto;")
    p.my-5.pt-5 Las entrevistas se pueden clasificar fundamentalmente, en:
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
      .row(titulo="Entrevista estructurada")
        .col-12
          p.mb-4 Las preguntas en esta entrevista se deciden, previamente, de acuerdo con el detalle de informacin requerida.
          .list.align-items-star.mt-2
            .list__item
              .list__item__icon
                img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
            .list__txt
              p.mt-2 Recoge de forma sistemtica y precisa la mayor informacin sobre los aspectos que quiere explorar.
          .list.align-items-star.mt-2
            .list__item
              .list__item__icon
                img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
            .list__txt
              p.mt-2 Las preguntas son prefijadas y definidas, las respuestas son esperadas e incluso se le dan al entrevistado en forma de varias opciones.
          .list.align-items-star.mt-2
            .list__item
              .list__item__icon
                img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
            .list__txt
              p.mt-2 Las etapas son planificadas.
          .list.align-items-star.mt-2
            .list__item
              .list__item__icon
                img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
            .list__txt
              p.mt-2 La interpretacin de las respuestas se realiza de acuerdo con unos criterios establecidos.
          .row.mt-5
            .col-lg-6
              a.anexo.mb-4(:href="obtenerLink('/downloads/Anexo1.pdf')" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-pdf.svg")
                .anexo__texto
                  p #[b Anexo.] Entrevista estructurada.
      div(titulo="Entrevista semiestructurada")
        .row
          .col-12
            p.mb-4 Esta presenta un grado mayor de flexibilidad que la estructurada, debido a que parten de preguntas planeadas, que pueden ajustarse a los entrevistados. Su ventaja es la posibilidad de adaptarse a los sujetos con enormes posibilidades para motivar al interlocutor, aclarar trminos, identificar ambigedades y reducir formalismos.
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 Las preguntas, desarrollo e interpretacin se planifican previamente, pero con un cierto grado de libertad de accin para abordar temas que pueden surgir durante la misma.
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 Se suele utilizar un protocolo para facilitar al entrevistador seguir un modelo preestablecido.

      div(titulo="Entrevista no estructurada")
        .row
          .col-12
            p.mb-4 Las entrevistas no estructuradas suelen describirse como conversaciones mantenidas con un propsito en mente.
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 No se estructura ni planifica previamente.
            .list.align-items-star.mt-2.mb-4
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 Es la ms gil y la que proporciona ms informacin en general, pero requiere un cierto dominio por parte del entrevistador.
            p.mb-4 En el material complementario se pueden revisar ejemplo de entrevistas.
    h5 1.2.2. Encuesta.
    .row.mb-5
      .col-lg-7
        .bloque-texto-b.p-4
          .bloque-texto-b__texto.h3.mb-0 Los cuestionarios son herramientas ampliamente utilizadas para recoger datos de sondeos y pueden ser administradas sin la presencia del investigador
          br
          p(style="position: relative;") - (Cohen, 2011, p. 377).
      .col-lg-5.mt-4.mt-lg-0
        p Pueden variar en cuanto a propsito, diseo y apariencia, y consisten en listas de preguntas escritas. Los individuos participantes en la investigacin suelen leer los mismos listados de preguntas, por lo que esto permite consistencia y precisin al analizar las respuestas, adems de facilitar el proceso. Una de las ventajas ms destacadas de los cuestionarios es que simplifican el proceso de la obtencin de datos, preguntando directamente a los individuos participantes para obtener datos de forma rpida y directa y se pueden aplicar a un gran nmero de sujetos.
    p.mb-4 Los datos que se obtienen a travs de los cuestionarios suelen estar clasificados en dos categoras: hechos y opiniones (Denscombe, 2010, p. 156). La informacin relacionada con los hechos no requiere el juicio o la actitud personal de los sujetos participantes, pero la informacin obtenida a travs de las opiniones implica creencias, puntos de vista y preferencias de los sujetos participantes.
    .row
      .col-6.p-4.bg-color2
        figure
          img(src='@/assets/curso/img9.svg', style="max-width: 20%; margin: auto;")
        h5.text-center.my-4 Tipos de preguntas
        p La distincin ms general entre los tipos de preguntas de los #[b cuestionarios, adems de hechos y opiniones, es la de preguntas abiertas y cerradas]; las preguntas abiertas son aquellas en las que no se especifica ninguna respuesta para elegir y se deja abierta a la eleccin del participante para que escriba en ella. Las preguntas cerradas son las que ofrecen ya unas respuestas predeterminadas para su eleccin.
      .col-6.p-4.bg-color3
        figure
          img(src='@/assets/curso/img10.svg', style="max-width: 11%; margin: auto;")
        h5.text-center.my-4 Tipos de respuestas
        p Las respuestas de escala son las ms comunes en los cuestionarios de investigacin ya que implican al participante en una valoracin o evaluacin de las respuestas objetivo por medio de varias opciones en las que tienen que marcar dentro de una escala la importancia de cada una. Esa escala de valoracin indica diferentes grados en una categora y puede ser de diversa naturaleza; por ejemplo, puede valorar una categora indicando si es #[b bueno] o #[b malo], #[b frecuente] o #[b infrecuente], #[b importante] o #[b poco importante] o tambin pueden valorar opiniones: #[b completamente de acuerdo] o #[b en desacuerdo]. El nmero de opciones ms comn es el de cinco, por ser un nmero impar, ya que existe una tendencia generalizada a seleccionar la opcin intermedia (Dornyei, 2010).
    .row.my-5
      .col-lg-6
        a.anexo.mb-4(href="https://www.youtube.com/watch?v=mwnQuUi9014" target="_blank")
          .anexo__icono
            img(src="@/assets/template/icono-link.svg")
          .anexo__texto
            p #[b Anexo.] Tipos de preguntas.
    h5 1.2.3. Observacin.
    p.mb-4 Esta permite la obtencin de datos para emprender una investigacin de tipo cualitativo, no desde el punto de vista de lo que los sujetos dicen, sino que es la evidencia directa de lo que ve y percibe el investigador en un escenario de primera mano (Denscombe, 2010).
    p.mb-4 Por su parte, Selltiz (citado por Hernndez, Fernndez y Baptista, 2006, p. 229), al referirse a la observacin, recomienda que para que esta se convierta en una tcnica como tal, debe cumplir con cuatro condiciones:
    .list.align-items-start.pl-4
      .list__item
        .list__item__bulletNumber
          span 1.
      .list__txt
        p.mb-0.mt-2 Debe servir a un objeto formulado de investigacin.
    .list.align-items-start.mt-2.pl-4
      .list__item
        .list__item__bulletNumber
          span 2.
      .list__txt
        p.mb-0.mt-2 Debe de ser planificada sistemticamente.
    .list.align-items-start.mt-2.pl-4
      .list__item
        .list__item__bulletNumber
          span 3.
      .list__txt
        p.mb-0.mt-2 Debe estar controlada y relacionada con proposiciones generales.
    .list.align-items-start.mt-2.pl-4.mb-5
      .list__item
        .list__item__bulletNumber
          span 4.
      .list__txt
        p.mb-0.mt-2 Debe ser sujeta a comprobaciones y controles de validez y fiabilidad.
    p.mb-4 De acuerdo con lo anterior, se puede asumir que la observacin:
    .list.align-items-star.mt-2
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Tiene la caracterstica de seguir normas, reglas y procedimientos.
    .list.align-items-star.mt-2
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Permite a los sujetos y objetos establecer relaciones de manera directa.
    figure.my-5
      img(src='@/assets/curso/img11.png', style="max-width: 100%; margin: auto;")
    p.mb-4 Para el caso de obtencin de requerimientos del software la observacin nos sirve para estudiar el entorno de trabajo de los usuarios, clientes e interesados de proyecto (stakeholders) y para documentar la situacin actual de procesos de negocio.
    p En la siguiente figura, se pueden revisar los tipos de observacin.
    figure.my-5
      img(src='@/assets/curso/img12.png', style="max-width: 100%; margin: auto;")
      figcaption Figura 3 / Tipos de observacin.
    p.mb-4 Ahora bien, para llevar a cabo la observacin, el observador puede utilizar como instrumento #[b la gua de observacin], la cual le permite situarse de manera sistemtica en aquello que realmente es objeto de estudio para la investigacin; tambin es el medio que conduce la recoleccin y obtencin de datos e informacin de un hecho o fenmeno.
    .bloque-texto-a.p-4.p-md-5.mb-5
      .row.m-0.align-items-center.justify-content-between
        .col-lg-4.mb-4.mb-lg-0
          .h2.mb-0 Tamayo (2004) define a la gua de observacin como:
        .col-lg-7
          .bloque-texto-a__texto.p-4
            p Un formato en el cual se pueden recolectar los datos en sistemtica y se pueden registrar en forma uniforme, su utilidad consiste en ofrecer una revisin clara y objetiva de los hechos, agrupa los datos segn necesidades especficas, se hace respondiendo a la estructura de las variables o elementos del problema (p. 172).
    p.mb-4 Para elaborar la gua de observacin se ha de disear el contenido de la observacin; el cual debe incluir por lo menos los siguientes aspectos:
    .list.align-items-start.pl-4
      .list__item
        .list__item__bulletNumber
          span 1.
      .list__txt
        p.mb-0.mt-2 Datos y caractersticas de los sujetos a evaluar.
    .list.align-items-start.mt-2.pl-4
      .list__item
        .list__item__bulletNumber
          span 2.
      .list__txt
        p.mb-0.mt-2 Propsitos de la observacin o de las observaciones a realizar.
    .list.align-items-start.mt-2.pl-4
      .list__item
        .list__item__bulletNumber
          span 3.
      .list__txt
        p.mb-0.mt-2 Temporalidad de la observacin.
    .row.my-5
      .col-lg-6
        a.anexo.mb-4(:href="obtenerLink('/downloads/Anexo2.pdf')" target="_blank")
          .anexo__icono
            img(src="@/assets/template/icono-link.svg")
          .anexo__texto
            p #[b Anexo.] Ficha de observacin.
    h5 1.2.4. Sesiones grupales.
    .row.align-items-center.mb-5
      .col-lg-6
        p.mb-4 Es un proceso por el cual se llevan a cabo reuniones en grupo altamente estructuradas que convocan, en una misma sala, a los usuarios de un sistema, los propietarios del sistema y a los analistas durante un amplio periodo de tiempo. Los objetivos de esta tcnica son esencialmente los mismos que los de las entrevistas, con la salvedad de necesitar ms analistas para llevarlos a cabo. 
        p Dentro de las sesiones de trabajo en grupo se encuentran tcnicas como la #[b lluvia de ideas, las sesiones JAD y el mtodo Delphi].
      .col-lg-6.mt-4.mt-lg-4
        figure
          img(src='@/assets/curso/img13.png', style="max-width: 70%; margin: auto;")
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
      .row(titulo="Lluvia de ideas").align-items-center
        .col-2
          figure
            img(src='@/assets/curso/img11.svg', style="max-width: 70%; margin: auto;")
        .col-10
          p Tambin denominada tormenta de ideas o incluso brainstorming. Faickney (1939) investig sobre diferentes maneras de generar creatividad. Se percat de que la mejor manera de ser creativo en una empresa es a travs de la interaccin y el trabajo en equipo; todos juntos podan dar sus opiniones y sugerencias sobre un tema determinado. Cre de esta manera la lluvia de ideas.
      div(titulo="Sesiones JAD (Joint Application Design)")
        .row.align-items-center
          .col-2
            figure
              img(src='@/assets/curso/img12.svg', style="max-width: 70%; margin: auto;")
          .col-10
            p.mb-4 Es un proceso usado para reunir requerimientos en el desarrollo de nuevos sistemas de informacin para una compaa. #[b El proceso JAD consiste en un taller donde los trabajadores del conocimiento y los especialistas en tecnologas de informacin se renen], algunas veces durante varios das, para definir y revisar los requerimientos de negocio para el sistema. Los asistentes incluyen oficiales de administracin de alto nivel, quienes se aseguran de que el producto provea los reportes y la informacin requerida al final, esto acta como #[b un proceso de administracin] que permite que los departamentos de servicios de informacin corporativa trabajen ms eficientemente con los usuarios en un marco de tiempo ms reducido. 
            p A travs de los talleres JAD, los trabajadores del conocimiento y los especialistas en tecnologas de informacin pueden resolver cualquier dificultad o diferencias entre las posturas referentes al nuevo sistema de informacin. #[b El taller sigue una detallada agenda para lograr garantizar que todas las incertidumbres entre los grupos sean cubiertas y para ayudar a prevenir cualquier falla en la comunicacin], estas fallas de comunicacin pueden provocar repercusiones mucho ms serias si no se identifican a tiempo. Al final, este proceso resultar en un nuevo Sistema de Informacin viable y orientado tanto a diseadores como a usuarios.
      div(titulo="Mtodo Delphi")
        .row.align-items-center
          .col-2
            figure
              img(src='@/assets/curso/img13.svg', style="max-width: 70%; margin: auto;")
          .col-10
            .mb-4 Es un #[b mtodo de estructuracin de un proceso de comunicacin grupal] que consiste en la seleccin de un grupo de expertos a los que se les pregunta su opinin frente a ciertas temticas.
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 #[b Fase uno.] Formulacin del problema: se define el campo de investigacin.
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 #[b Fase dos.] Eleccin de expertos: el experto se elige segn su preparacin y su capacidad de proyeccin.
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 #[b Fase tres.] Elaboracin de cuestionarios: las preguntas deben hacerse de acuerdo con la temtica que se quiere obtener.
            .list.align-items-star.mt-2
              .list__item
                .list__item__icon
                  img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
              .list__txt
                p.mt-2 #[b Fase cuatro.] Desarrollo y explotacin de resultados: el cuestionario se entrega a los expertos para ser contestado por ellos.
    #t_1_3
    .titulo-segundo.pt-5
      .h4 1.3. Herramientas para captura de requisitos
    p.mb-5 Existen varias herramientas para la captura de requisitos potenciales de un nuevo sistema o una actualizacin de software, a continuacin, se explican las ms utilizadas:
    h5 1.3.1. Diagrama de casos de uso.
    p.mb-4 Al momento de desarrollar un proyecto se debe pensar en cules sern las principales funcionalidades que el software debe permitir llevar a cabo y quines sern los que podrn ejecutar dichas funcionalidades. #[b La identificacin de estos elementos se puede visualizar de manera efectiva a travs de la elaboracin de diagramas de casos de uso]; estos diagramas, que son elaborados durante las etapas iniciales de un proyecto, se convierten en referente para cada una de las etapas siguientes del desarrollo del proyecto.
    p.mb-5 Componentes. En los diagramas de casos de uso, se observan los siguientes componentes.
    h5 Actor
    p.mb-4 se representa mediante un hombre de palo. Este se emplea para indicar el tipo de usuario del sistema que podr ejecutar alguna funcin.
    figure.my-5
      img(src='@/assets/curso/img14.png', style="max-width: 100%; margin: auto;")
    h5 Caso de uso
    p.mb-4 se representa mediante un valo e indica una funcin que el sistema debe proveer.
    figure.my-5
      img(src='@/assets/curso/img15.png', style="max-width: 25%; margin: auto;")
    p.mb-4 Para ejemplificar un proceso se puede emplear un verbo conjugado en infinitivo y que represente la funcin a realizar (administrar, gestionar, registrar, entre otros). A continuacin, se presenta un ejemplo, en el cual se presenta un diagrama de casos de uso de la sistematizacin de un centro mdico.
    .list.align-items-star.mt-2
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Administrar datos pacientes.
    .list.align-items-star.mt-2
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Administrar datos tratamientos.
    .list.align-items-star.mt-2
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Gestionar citas.
    .list.align-items-star.mt-2.mb-5
      .list__item
        .list__item__icon
          img.img-a1(src="@/assets/template/icon-list1.svg").img-fluid  
      .list__txt
        p.mt-2 Generar reportes.
    h5 Representacin grfica
    figure.my-5
      img(src='@/assets/curso/img16.png', style="max-width: 100%; margin: auto;")
      figcaption Figura 7 / Caso de uso centro mdico.
    h5 Identificacin de casos de uso
    p.mb-5 En el ejemplo anterior se observan los casos de uso identificados en el sistema, es decir, las funcionalidades que el sistema va a proveer (administrar datos pacientes, administrar datos tratamientos, gestionar citas, generar reportes).
    h5 Identificacin de actores
    p.mb-5 Los actores son los usuarios que podrn ejecutar los casos de uso, en el ejemplo anterior, se identificaron dos actores (mdico y empleado).
    h5 Documentacin
    p.mb-5 La tcnica de casos de uso requiere adems de construir el diagrama de casos de uso, la descripcin de estos. Esta descripcin permite detallar el flujo de eventos que se da entre el Sistema y el Actor para llevar a cabo el caso de uso. A continuacin, se presenta el formato diligenciado de acuerdo con el ejemplo del centro mdico.
    .tarjeta.tarjeta.bg-color4.p-4.p-md-5.mb-5.shadow
      .row.justify-content-around.align-items-center
        .col-8.col-sm-6.col-md-4.mb-4.mb-md-0
          img(src="@/assets/curso/img14.svg")
        .col-md.col-lg-6
          .h2 Documentacin caso de uso
          p.mb-4 A continuacin, se presenta el formato diligenciado de acuerdo con el ejemplo del centro mdico.
          a.boton.boton--b(:href="obtenerLink('/downloads/Anexo4.pdf')" target="_blank" type="application/pdf")
            span(style="color: #000") Descargar
            i(style="color: #000").fas.fa-file-download
          p.mt-3.mb-4 #[b Nota: ]tomado de Gutierrez (s.f.)
    h5 1.3.2. Historias de usuario.
    p.mb-4 Las historias de usuario son utilizadas en los mtodos agiles para la especificacin de requisitos, son una descripcin breve de una funcionalidad software tal y como la percibe el usuario (Cohn, 2004).
    p.mb-5 El formato para las historias de usuario Scrum se basan en una regla de tres palabras:
    .row.justify-content-center.mb-5
      .col-10.col-lg-8
        figure
          img(src='@/assets/curso/img17.png', style="max-width: 100%; margin: auto;")
          figcaption Figura 8 /Regla de tres palabras.
        p.mt-3.mb-4 #[b Nota: ]tomado de Martin (s.f.)
    p.mb-4 As, el #[b &lt;rol&gt;] que se escoja que va a utilizar la aplicacin software, requiere de una #[b &lt;Accin&gt;] / #[b &lt;evento&gt;] que ocurra, porque se desea cubrir una #[b &lt;funcionalidad&gt;]. Corto y conciso, directo y claro.
    p.mb-5 En las siguientes figuras se presentan ejemplos de historias de usuario.
    .row.justify-content-center.mb-5
      .col-10.col-lg-8
        figure
          img(src='@/assets/curso/img18.png', style="max-width: 100%; margin: auto;")
          figcaption Figura 9 /Ejemplos historias de usuario.
          p.mt-3.mb-4 #[b Nota: ]tomado de fatto www.fattocs.com <a href="http://i.ytimg.com/vi/Zi9E1aUO_1U/hqdefault.jpg" target="_blanck">http://i.ytimg.com/vi/Zi9E1aUO_1U/hqdefault.jpg</a>
    h5 Conversacin para explicar mejor la historia de usuario
