            p Los #[i storyboards] son un tipo de prototipos muy utilizados, #[b consiste bsicamente en ir mostrando en una secuencia de imgenes un proceso, accin o ejercicio que se puede realizar en el sistema una vez terminado], las imgenes van mostrando la evolucin del sistema conforme el usuario interacta con el sistema.
    p.mb-4 Con esta tcnica se pretende crear diferentes vistas del sistema en las primeras etapas de su implementacin de la manera ms rpida y barata posible [SUT02].
    p.mb-4 Una forma muy comn de ejemplificar los storyboards es con las revistas de cmics, ya que van mostrando una secuencia de imgenes en cuadros con un orden establecido que permiten entender la lnea de la historia contada. La tcnica storyboard permite generar modelos o esquemas visuales como esbozos de interfaces grficas de usuario (GUI).
