        .col-lg-7
          .bloque-texto-a__texto.p-4
            p.mb-4 Las herramientas de modelado de sistemas informticos se emplean para la creacin de modelos de sistemas que ya existen o que se desarrollarn; estas herramientas permiten crear un #[b "simulacro"] del sistema, a bajo coste y riesgo mnimo. #[b A bajo costo porque, es un conjunto de grficos y textos que representan el sistema].
            p El Lenguaje Unificado de Modelado (UML, por sus siglas en ingls, Unified Modeling Language) #[b es el lenguaje de modelado de sistemas de software ms conocido y utilizado en la actualidad]; UML es un lenguaje grfico que permite especificar, modelar, construir y documentar los elementos que forman un sistema software.
    p.mb-4 De otra parte, las herramientas CASE son un conjunto de programas y procesos guiados, que ayudan a los analistas, desarrolladores, ingenieros de software y diseadores en una o todas las etapas que comprende un ciclo de vida, con el objetivo de facilitar el desarrollo de software. El objetivo general de estas herramientas es acelerar el proceso para el que han sido diseadas, es decir, para automatizar o apoyar una o ms fases del ciclo de vida del desarrollo de sistemas. CASE proporciona un conjunto de herramientas semiautomatizadas y automatizadas que estn creando una nueva cultura de ingeniera en muchas empresas. Las herramientas CASE se disearon para aumentar la productividad en el desarrollo de software y reducir su costo.
    p.mb-4 Existen muchas herramientas para modelado tanto en libres como con derechos comerciales; a rengln seguido se listan las herramientas CASE ms utilizadas:
