      .py-3.py-md-4(titulo="Gliffy")
        .row
          .col-12
            p.mb-4 La aplicacin en lnea Gliffy es una herramienta de diagramas UML basada en la nube. Apareci por primera vez en 2006 y se trata de una herramienta de modelado que crea todo tipo de diagramas, tales como diagramas de flujo, diagramas de Venn y, por supuesto, diagramas UML. La herramienta en lnea fue escrita en HTML5 y es bastante popular gracias a su rapidez de reaccin. Es de anotar que antes de que Gliffy pasara por la fase beta en 2007, la empresa homnima cooper con el grupo de software australiano Atlassian. Ya en 2006, su software de colaboracin Confluence integr un plugin de Gliffy y, ms tarde, el equipo de Gliffy desarroll un plugin para Jira. Google Workspace y Drive de Google tambin integran esta herramienta UML.
            figure
              img(src='@/assets/curso/img24.png', style="max-width: 100%; margin: auto;")
              figcaption Figura 13 /Herramienta Gliffy.
            p.mt-3 #[b Nota: ]Tomado de <a href="https://www.gliffy.com/" target="_blank">https://www.gliffy.com/</a>
      .py-3.py-md-4(titulo="ArgoUML")
        .row
          .col-12
            p.mb-4 Ha sido durante mucho tiempo una de las herramientas UML gratuitas de cdigo abierto ms populares para el escritorio y aunque ya no se mantiene, muchos modeladores continan usando el programa para tareas ms pequeas. Su software es multiplataforma, cuyo el requisito mnimo es Java 5 ArgoUML soporta todos los tipos de diagramas de la versin 1.4 de UML y perfiles UML. El programa tambin ofrece algunas formas decorativas que no forman parte del estndar UML.
            p.mb-4 Adems, aunque esta herramienta UML est disponible como descarga gratuita, ArgoUML #[b soporta una amplia gama de lenguajes de programacin] cuyo cdigo puede generarse a partir de un diagrama. La 	ingeniera inversa tambin es posible para Java, C++, PHP, C# y SQL. El programa reconoce otros idiomas como Delphi o Ruby cuando los agrega como extensiones a la carpeta de archivos ArgoULM.
            figure
